.background {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 123;
}

.Modal {
    position: relative;
    z-index: 1400;
    top: 20px;
    margin: auto;
    width: 60%;
    min-width: 360px;
    max-width: 800px;
    opacity: 1;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: white;
}

.Modal>header {
    border-bottom: 1px solid #ddd;
    font-weight: 500;
    padding: 20px;
}

.Modal>div.content {
    padding: 20px;
}

.Modal>footer {
    border-top: 1px solid #ddd;
    padding: 10px 15px 15px 20px;
    text-align: right;
}
.Modal>footer>button {
    margin: 0 5px;
}